@import url(https://fonts.google.com/specimen/Manrope);
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    background: hsl(218, 23%, 16%);
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.contents{
   background: hsl(217, 19%, 24% );
   max-width: 550px;
   display: flex;
   align-items: center;
   justify-content: center;
   text-align: center;
   flex-direction: column;
   padding: 2.5rem 3rem 2rem 3rem;
   border-radius: 15px;
}
p{
    margin-bottom:20px ;
    color: hsl(150, 100%, 66%);
    font-family: 'Manrope',sans-serif;
    letter-spacing: 2px;
    font-weight: 800;
}
h2{
    margin-bottom:30px ;
    color: hsl(193, 38%, 86%);
    font-family: 'Manrope',sans-serif;
    font-weight: 600;
    font-size: 24px;
}
.codedesk{
    display: flex;
}
.codemob{
    display: none;
}
.dise{
    background:hsl(150, 100%, 66%) ;
    height: 54px;
    width: 54px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    bottom: -3.8rem;
    cursor: pointer;
}
.dise:hover{
    box-shadow: 0 0 40px 0 rgba(82, 252, 169, 1);
    transition: 0.2s ease-in;
}

@media all and (max-width:375px) {
    .contents{
        max-width: 250px;
    }
    p{
        font-size: 14px;
    }
    h2{
        font-size: 18px;
    }
    .codedesk{
        display: none;
    }
    .codemob{
        display: flex;
    }

}
